* {
  margin: 0;
  padding: 0;
  font-family: Rubik, sans-serif;
}

*, :after, :before {
  box-sizing: border-box;
}

@media screen and (min-width: 767px) {
  ::-webkit-scrollbar {
    width: .8rem;
  }

  ::-webkit-scrollbar-track {
    opacity: .9;
    background-color: #9194a1;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #252b46;
    border-radius: .25rem;
  }
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100vh;
  isolation: isolate;
  flex-direction: column;
  display: flex;
}

@media screen and (max-width: 768px) {
  body {
    overflow: hidden;
  }
}

main {
  width: 100vw;
  scroll-behavior: smooth;
  overflow-x: hidden;
  overflow-y: auto;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a:visited {
  color: initial;
}

ul {
  list-style: none;
}

button {
  border: none;
  outline: none;
}

.attribution {
  text-align: center;
  background-color: #0000;
  font-size: 11px;
}

.attribution a {
  color: #3e52a3;
}

.hide {
  display: none !important;
}

.show {
  display: block;
  transform: translate(0%);
}

header {
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  padding: 2rem 1.5rem;
  display: flex;
}

header .logo {
  margin-right: auto;
}

@media screen and (min-width: 767px) {
  header .logo {
    order: 1;
  }

  header {
    padding: 2rem 9rem;
  }
}

@media screen and (max-width: 1140px) {
  header {
    padding-left: 3rem;
    padding-right: 3rem;
  }
}

.navigation.slide {
  transition: transform .8s;
}

@media screen and (max-width: 768px) {
  .navigation {
    z-index: 100;
    background-color: #252b46e6;
    flex-direction: column;
    gap: 1.25rem;
    padding-top: 2rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    display: flex;
    position: absolute;
    inset: 0;
  }

  .navigation[aria-hidden="false"] {
    transform: translateY(0%);
  }

  .navigation[aria-hidden="false"] ~ .sidenav-toggler, .navigation[aria-hidden="false"] ~ .logo {
    display: none;
  }

  .navigation[aria-hidden="true"] {
    transform: translateY(-100%);
  }

  .navigation ul {
    margin-top: 1rem;
  }

  .navigation ul .nav-link {
    text-align: center;
    border-top: 1px solid #9194a1;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }

  .navigation ul .nav-link:nth-last-of-type(1) {
    border-bottom: 1px solid #9194a1;
  }

  .navigation ul .nav-link a {
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: 1.25rem;
    font-weight: 400;
  }

  .navigation .login a {
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: 2px;
    border: 2px solid #fff;
    border-radius: .25rem;
    padding: .6rem 1rem;
    font-size: 1.25rem;
    display: block;
  }

  .navigation .socials {
    align-items: center;
    gap: 3rem;
    margin: auto auto 4rem;
    display: flex;
  }
}

@media screen and (min-width: 767px) {
  .navigation {
    order: 2;
    gap: 2rem;
    display: flex;
  }

  .navigation ul {
    gap: 2rem;
    display: flex;
  }

  .navigation ul .nav-link a {
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #252b46;
    font-size: 12px;
    font-weight: 500;
    transition: color .5s;
  }

  .navigation ul .nav-link a:hover {
    color: #fa5757;
  }

  .navigation .login a {
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
    background-color: #fa5757;
    border-radius: .5rem;
    padding: .75rem 2rem;
    font-size: 14px;
    transition: color .5s;
    box-shadow: 0 2px 5px 1px #9194a1;
  }

  .navigation .login a:hover {
    background-color: inherit;
    color: #fa5757;
    border: 2px solid #fa5757;
    padding-left: 1.9rem;
    padding-right: 1.9rem;
  }

  .navigation .control, .navigation .socials {
    display: none;
  }
}

.navigation .control {
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .navigation .control {
    display: flex;
  }
}

.navigation .control .logo {
  transform-origin: 0;
  transform: scale(1.3);
}

@media screen and (max-width: 1140px) {
  .navigation .control .logo {
    transform: scale(1);
  }
}

.navigation .control .close-btn {
  transform-origin: 100%;
  transform: scale(1.3);
}

.sidenav-toggler {
  display: inline-block;
}

@media screen and (min-width: 767px) {
  .sidenav-toggler {
    display: none;
  }
}

section.about {
  width: 100%;
  isolation: isolate;
  align-items: center;
  margin-top: 3.5rem;
  display: flex;
  position: relative;
}

@media screen and (max-width: 768px) {
  section.about {
    align-items: inherit;
    justify-content: inherit;
    flex-direction: column-reverse;
  }
}

section.about:after {
  --width: 55rem;
  content: "";
  width: var(--width);
  height: calc(var(--width) / 3);
  border-radius: calc(var(--width) / 3 / 2);
  z-index: -1;
  background-color: #5368df;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(40%);
}

@media (max-width: 1400px) {
  section.about:after {
    --width: 48rem;
  }
}

@media (max-width: 1200px) {
  section.about:after {
    --width: 47rem;
  }
}

@media (max-width: 1118px) {
  section.about:after {
    --width: 46rem;
  }
}

@media screen and (max-width: 768px) {
  section.about:after {
    --width: 43rem;
  }
}

@media (max-width: 620px) {
  section.about:after {
    --width: 42rem;
  }
}

@media (max-width: 540px) {
  section.about:after {
    --width: 40rem;
  }
}

@media (max-width: 1000px) {
  section.about:after {
    bottom: 5%;
    transform: translate(60%);
  }
}

@media screen and (max-width: 768px) {
  section.about:after {
    bottom: 38%;
    transform: translate(15%);
  }
}

@media (max-width: 620px) {
  section.about:after {
    bottom: 37%;
    transform: translate(15%);
  }
}

@media (max-width: 540px) {
  section.about:after {
    bottom: 50%;
    transform: translate(50%);
  }
}

section.about:nth-child(1) {
  width: 50%;
}

.about__description {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
}

.about__description .about__description--title {
  max-width: fit-content;
  color: #252b46;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 767px) {
  .about__description .about__description--title {
    letter-spacing: 2px;
    min-width: 100%;
    font-size: 3rem;
    font-weight: 500;
    line-height: 1;
  }
}

.about__description .about__description--desc {
  max-width: 45ch;
  white-space: pre-line;
  color: #9194a1;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  line-height: 1.6;
  overflow: hidden;
}

@media screen and (min-width: 767px) {
  .about__description .about__description--desc {
    white-space: normal;
    max-width: 50ch;
    font-size: 18px;
    line-height: 1.4;
  }
}

.about__description .about__buttons {
  gap: .75rem;
  display: flex;
}

@media screen and (max-width: 768px) {
  .about__description .about__buttons {
    justify-content: center;
  }
}

.about__description .about__buttons button {
  border-radius: .25rem;
  padding: 1rem;
  font-size: .8rem;
  font-weight: 500;
  transition: all .3s ease-in-out .1s;
  box-shadow: 1px 4px 10px 1px #d4d9f7;
}

@media screen and (min-width: 767px) {
  .about__description .about__buttons button {
    font-size: 1rem;
    font-weight: 500;
  }
}

.about__description .about__buttons .about__buttons--chrome {
  color: #fff;
  background-color: #5368df;
}

.about__description .about__buttons .about__buttons--chrome:hover {
  border-style: solid;
  border-width: 2px;
  border-color: inherit;
  color: #5368df;
  background-color: #fff;
  border-color: #5368df;
  padding: .8rem .85rem;
}

.about__description .about__buttons .about__buttons--firefox {
  color: #252b46;
}

.about__description .about__buttons .about__buttons--firefox:hover {
  border-style: solid;
  border-width: 2px;
  border-color: inherit;
  color: #252b46;
  background-color: #fff;
  border-color: #252b46;
  padding: .8rem .85rem;
}

@media screen and (min-width: 767px) {
  .about__description .about__buttons {
    gap: 1rem;
  }

  .about__description {
    text-align: left;
    justify-content: flex-start;
    gap: 1.25rem;
  }
}

section.features {
  margin-top: 8rem;
}

.features__intro {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
}

.features__intro .features__intro--title {
  max-width: fit-content;
  color: #252b46;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.5rem;
}

@media screen and (min-width: 767px) {
  .features__intro .features__intro--title {
    min-width: 100%;
    font-size: 1.75rem;
    font-weight: 500;
    line-height: 1;
  }
}

.features__intro .features__intro--desc {
  max-width: 50ch;
  white-space: pre-line;
  color: #9194a1;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  line-height: 1.6;
  overflow: hidden;
}

@media screen and (min-width: 767px) {
  .features__intro .features__intro--desc {
    white-space: normal;
    max-width: 50ch;
    font-size: 18px;
    line-height: 1.4;
  }
}

.features__tabs {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 2rem;
  display: flex;
}

.features__tabs .features__tabs--tab {
  text-align: center;
}

@media screen and (max-width: 768px) {
  .features__tabs .features__tabs--tab {
    width: 80%;
  }

  .features__tabs .features__tabs--tab:nth-child(1), .features__tabs .features__tabs--tab:nth-child(2), .features__tabs .features__tabs--tab:nth-child(3) {
    border-top: 1px solid #c8c9d0;
  }

  .features__tabs .features__tabs--tab:last-child {
    border-bottom: 1px solid #c8c9d0;
  }
}

.features__tabs .features__tabs--tab button {
  color: #9194a1;
  background-color: #fff;
  padding: 1rem .25rem;
  font-size: 1rem;
  transition: color .5s linear;
  display: inline-block;
  position: relative;
}

.features__tabs .features__tabs--tab button:after {
  content: "";
  width: 100%;
  height: 5px;
  transform-origin: center;
  background-color: #fa5757;
  transition: transform .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0%);
}

@media screen and (max-width: 768px) {
  .features__tabs .features__tabs--tab button:after {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
  }
}

@media screen and (min-width: 767px) {
  .features__tabs .features__tabs--tab button:after {
    height: 3px;
  }
}

.features__tabs .features__tabs--tab button.active {
  color: #252b46;
}

.features__tabs .features__tabs--tab button.active:after {
  transform: scaleX(100%);
}

@media screen and (min-width: 767px) {
  .features__tabs .features__tabs--tab button {
    width: 100%;
    text-align: start;
    padding-left: 3rem;
    padding-right: 3rem;
  }
}

.features__tabs .features__tabs--tab:hover button {
  color: #fb8888;
}

@media screen and (min-width: 767px) {
  .features__tabs .features__tabs--tab {
    border: none;
    padding-left: 0;
    padding-right: 0;
  }

  .features__tabs {
    width: fit-content;
    border-bottom: 1px solid #c8c9d0;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 5rem;
    margin-left: auto;
    margin-right: auto;
    padding-left: 0;
    padding-right: 0;
  }
}

.features__slides-container {
  margin-bottom: 5rem;
  position: relative;
}

@media screen and (min-width: 767px) {
  .features__slides-container {
    margin-bottom: 18rem;
  }
}

.features__slides-container:after {
  --width: 55rem;
  content: "";
  width: var(--width);
  height: calc(var(--width) / 3);
  border-radius: calc(var(--width) / 3 / 2);
  z-index: -1;
  background-color: #5368df;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(-30%, 20%);
}

@media (max-width: 1400px) {
  .features__slides-container:after {
    --width: 48rem;
  }
}

@media (max-width: 1200px) {
  .features__slides-container:after {
    --width: 47rem;
  }
}

@media (max-width: 1118px) {
  .features__slides-container:after {
    --width: 46rem;
  }
}

@media screen and (max-width: 768px) {
  .features__slides-container:after {
    --width: 43rem;
  }
}

@media (max-width: 620px) {
  .features__slides-container:after {
    --width: 42rem;
  }
}

@media (max-width: 540px) {
  .features__slides-container:after {
    --width: 40rem;
  }
}

@media screen and (min-width: 767px) {
  .features__slides-container:after {
    bottom: 5%;
    transform: translate(-30%, -5%);
  }
}

@media (max-width: 1200px) {
  .features__slides-container:after {
    bottom: 5%;
    transform: translate(-30%, -20%);
  }
}

@media (max-width: 1118px) {
  .features__slides-container:after {
    bottom: 5%;
    transform: translate(-38%, -16%);
  }
}

@media screen and (max-width: 768px) {
  .features__slides-container:after {
    bottom: 38%;
    transform: translate(-15%, -22%);
  }
}

@media (max-width: 620px) {
  .features__slides-container:after {
    bottom: 37%;
    transform: translate(-20%, -20%);
  }
}

@media (max-width: 540px) {
  .features__slides-container:after {
    bottom: 20%;
    transform: translate(-50%, -80%);
  }
}

.features__slides {
  flex-wrap: nowrap;
  transition: transform .3s;
  display: flex;
  transform: translateX(0%);
}

.features__slide {
  width: 100%;
  min-width: 100%;
  align-items: initial;
  justify-content: initial;
  flex-direction: column;
  gap: 2rem;
  padding-left: 5%;
  padding-right: 5%;
  display: flex;
  position: relative;
}

@media screen and (min-width: 767px) {
  .features__slide {
    grid-template-columns: 1fr 1fr;
    gap: 6rem;
    padding-left: 10%;
    padding-right: 10%;
    display: grid;
  }
}

.features__slide--img {
  margin: initial;
}

.features__slide-content {
  text-align: center;
  flex-direction: column;
  gap: .75rem;
  padding-top: 2.25rem;
  display: flex;
}

.features__slide-content .features__slide-content--title {
  max-width: fit-content;
  letter-spacing: 1px;
  color: #252b46;
  mix-blend-mode: difference;
  margin-left: auto;
  margin-right: auto;
  font-size: 1.25rem;
  font-weight: 500;
}

@media screen and (min-width: 767px) {
  .features__slide-content .features__slide-content--title {
    margin-left: initial;
    margin-right: initial;
    min-width: 100%;
    font-size: 2rem;
    line-height: 1;
  }
}

.features__slide-content .features__slide-content--desc {
  max-width: 45ch;
  white-space: pre-line;
  color: #9194a1;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  line-height: 1.6;
  overflow: hidden;
}

@media screen and (min-width: 767px) {
  .features__slide-content .features__slide-content--desc {
    white-space: normal;
    max-width: 45ch;
    margin-left: initial;
    margin-right: initial;
    font-size: 18px;
    line-height: 1.4;
  }
}

.features__slide-content .features__slide-content--btn {
  width: max-content;
  color: #fff;
  background-color: #5368df;
  border-radius: .25rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem;
  font-size: 14px;
  font-weight: 400;
  box-shadow: 1px 4px 10px 1px #d4d9f7;
}

@media screen and (min-width: 767px) {
  .features__slide-content .features__slide-content--btn {
    margin-left: initial;
    margin-right: initial;
  }

  .features__slide-content {
    text-align: initial;
    justify-content: center;
    gap: 1.25rem;
  }
}

section.browsers {
  grid-template-columns: 1fr;
  margin-bottom: 10rem;
  display: grid;
}

.browsers__intro {
  text-align: center;
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.browsers__intro--title {
  color: #252b46;
}

@media screen and (min-width: 767px) {
  .browsers__intro--title {
    font-size: 2rem;
  }
}

.browsers__intro--desc {
  color: #9194a1;
  white-space: pre-line;
  max-width: 30ch;
  width: 30ch;
  margin-left: auto;
  margin-right: auto;
  font-size: 1rem;
  line-height: 1.6;
  overflow: hidden;
}

@media screen and (min-width: 767px) {
  .browsers__intro--desc {
    white-space: initial;
    max-width: 50ch;
  }
}

.browser__cards {
  max-width: initial;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  gap: 2rem;
  margin-top: 3rem;
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
  position: relative;
}

@media screen and (min-width: 767px) {
  .browser__cards {
    max-width: 80vw;
    width: 80vw;
    align-items: initial;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
    padding-left: auto;
    padding-right: auto;
  }
}

@media screen and (max-width: 1140px) {
  .browser__cards {
    max-width: 95vw;
    width: 95vw;
    margin-left: auto;
    margin-right: auto;
    padding-left: 5%;
    padding-right: 5%;
  }
}

.browser__card {
  min-width: 20rem;
  max-width: 20rem;
  background-image: url("bg-dots.6d3e8475.svg");
  background-position: 50% 72%;
  background-repeat: no-repeat;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  gap: 3rem;
  padding-top: 3rem;
  padding-bottom: 1rem;
  display: flex;
  position: relative;
  box-shadow: 0 5px 10px 1px #d4d9f7;
  background-size: contain !important;
}

.browser__card:before {
  content: "";
  width: 100%;
  border-radius: inherit;
  height: 10px;
  mix-blend-mode: lighten;
  background-color: #ffffff80;
  border: none;
  outline: none;
  margin-top: -1px;
  position: absolute;
  top: -10px;
  left: 0;
}

@media screen and (min-width: 767px) {
  .browser__card {
    max-width: initial;
    min-width: initial;
    margin-left: initial;
    margin-right: initial;
    width: 35%;
    background-position: 50% 75%;
  }

  .browser__card:nth-of-type(1) {
    margin-top: 0;
    margin-bottom: 0;
  }

  .browser__card:nth-of-type(2) {
    margin-top: 2rem;
    margin-bottom: -2rem;
  }

  .browser__card:nth-of-type(3) {
    margin-top: 4rem;
    margin-bottom: -4rem;
  }
}

.browser__card--info {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  display: flex;
}

.browser__card--info img {
  max-width: 25%;
  width: 25%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 767px) {
  .browser__card--info img {
    max-width: 35%;
    width: 35%;
    margin-left: auto;
    margin-right: auto;
  }
}

.browser__card--info .browser__card--info-text {
  line-height: 1.7;
}

.browser__card--info .browser__card--info-text h3 {
  color: #252b46;
  font-size: 1.25rem;
}

.browser__card--info .browser__card--info-text span {
  color: #9194a1;
}

.browser__card--btn {
  max-width: 80%;
  width: 80%;
  color: #fff;
  background-color: #5368df;
  border-radius: .5rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem 2rem;
  transition: background-color .5s linear 10ms;
  position: relative;
  box-shadow: 0 2px 10px 2px #d4d9f7;
}

.browser__card--btn:before {
  content: "";
  width: 100%;
  border-radius: inherit;
  height: 2px;
  mix-blend-mode: lighten;
  background-color: #ffffff80;
  border: none;
  outline: none;
  margin-top: -1px;
  position: absolute;
  top: -2px;
  left: 0;
}

.browser__card--btn:hover {
  max-width: 90%;
  width: 90%;
  border-style: solid;
